import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import {  
    mapState 
} from 'vuex';
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			navList:[],
			goodsList: [],
			newsList:[],
			brandList:[],
			showRigth:false,
			ss:false,
			sskeys:'',
			ssremen:[],
			sslishi:[],
			memberInfo:[]
		};
	},
	onLoad() {
		
	},
	onShow() {
		this.loadData();
		this.sslishi = uni.getStorageSync("cbe_sslishi");
		if(uni.getStorageSync('cbe_token')){
			this.token = uni.getStorageSync('cbe_token');
			this.getMemberInfo();
		}else{
			this.memberInfo = [];
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	methods: {
		bss(e){
			this.sskeys = e.detail.value;
		},
		shanchu(e){
			var sslishi = uni.getStorageSync("cbe_sslishi");
			var newsslishi = [];
			var ii = 0;
			for(var i=0;i<sslishi.length;i++){
				if(sslishi[i]!=e){
					newsslishi[ii] = sslishi[i];
					ii++
				}
			}
			this.sslishi = newsslishi;
			uni.setStorageSync("cbe_sslishi",newsslishi);
		},
		quSousuo(){
			if(this.sskeys!=""){
				uni.navigateTo({
					url:'/pages/index/search?keywords='+this.sskeys
				})
			}
		},
		sousuo(){
			this.ss = true;
		},
		quxiaoss(e){
			if(e.detail.value==''){
				this.ss = true;
			}
		},
		lianxi(){
			uni.showModal({
				title:"客服热线",
			    content: '400-004-5678',
				confirmColor:'#008AB0',
				confirmText:'现在拨打',
			    success: (e)=>{
			    	if(e.confirm){
						uni.makePhoneCall({
							phoneNumber: '400-004-5678'
						})
			    	}
			    }
			});
		},
		gonews(e){
			let detail = {
			    author_name: e.article_author,
			    cover: e.resp_img,
			    id: e.id,
			    post_id: e.id,
			    published_at: e.article_date,
			    title: e.article_title
			}
			uni.navigateTo({
			    url: "/pages/news/details?detailDate=" + encodeURIComponent(JSON.stringify(detail))
			})
		},
		navTo(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
			}
			uni.navigateTo({  
				url
			})  
		},
		navaTo(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
				uni.navigateTo({  
					url:url
				})
			}else{
				uni.switchTab({
					url:url
				}) 
			}
		}, 
		tabTo(url){
			uni.switchTab({
				url:url
			}) 
		},
		nnavaTo(url){
			uni.navigateTo({  
				url:url
			})
		},
		Toproduct(url){
			if(uni.getStorageSync('cbe_token')){
				uni.navigateTo({  
					url:url+"&utoken="+uni.getStorageSync('cbe_token')
				})
			}else{
				uni.navigateTo({  
					url:url
				})
			}
		},
		loadData() {
			uni.showLoading({
				title: '请稍后'
			})
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/home/index',
				data: {token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					this.carouselList = resa.data.banner;
					this.navList      = resa.data.nav;
					this.goodsList    = resa.data.product;
					this.newsList     = resa.data.news;
					this.brandList    = resa.data.brand;
					this.ssremen      = resa.data.rmlist;
					uni.hideLoading();
				}
			})
		},
		getProduct(){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/product/lists',
				data: {token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					this.goodsList = one.data.data;
				}
			})
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		shoucang(id,index){
			if(uni.getStorageSync('cbe_token')){
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/product/shoucang',
					data: {id:id,token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						var one = resa.data;
						if(one.code==0){
							if(this.goodsList[index].isShoucang==0){
								this.goodsList[index].isShoucang = 1;
							}else{
								this.goodsList[index].isShoucang = 0;
							}
							uni.showToast({
								title:one.msg,icon:'success',mask:true,duration: 3000
							});
						}
					}
				})
			}else{
				uni.showModal({
					title:"温馨提示",
				    content: '您还没有登录，无法进行收藏操作，是否登录？',
					confirmColor:'#008AB0',
					confirmText:'立即登录',
					cancelText:'再看看',
				    success: (e)=>{
				    	if(e.confirm){
							uni.navigateTo({
								url:"/pages/public/login"
							})
				    	}
				    }
				});
			}
		},
		show(e) {
			if (e === 'left') {
				this.showLeft = true
			} else {
				this.showRigth = true
			}
		},
		hide() {
			this.showLeft = false
			this.showRigth = false
			this.ss = false;
		},
		closeDrawer(e){
			if (e === 'left') {
				this.showLeft = false
			} else {
				this.showRigth = false
			}
		},
		getMemberInfo(){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/member/info',
				data: {token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					var one = resa.data;
					if(one.code==0){
						one.one['phone'] = one.one.mobile.substr(0, 3) + '****' + one.one.mobile.substr(7);
						if(one.one.CNcardNo){
							one.one['CNcardNo'] = one.one.CNcardNo.replace(/^(.{4})(?:\d+)(.{4})$/,"$1******$2");
						}
						this.memberInfo = one.one;
						this.memberInfo.length = 11;
						console.log(one.one);
					}else{
						this.memberInfo = [];
						uni.removeStorageSync('cbe_token');
					}
				},
				fail: (resa) => {
					this.getMemberInfo();
				}
			})
		}
	},













}
